import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Gallery from '../views/Gallery.vue'
import PaintingDetail from '../views/PaintingDetail.vue'
import Exhibiton from '../views/Exhibition.vue'
import AboutMe from '../views/AboutMe.vue'
import Contact from '../views/Contact.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/gallery',
    name: 'Gallery',
    component: Gallery,
  },
  {
    path: '/gallery/:id',
    name: 'Painting Detail',
    component: PaintingDetail,
  },
  {
    path: '/exhibiton',
    name: 'Exhibiton',
    component: Exhibiton,
  },
  {
    path: '/about_me',
    name: 'About Me',
    component: AboutMe,
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact,
  },
]

const router = new VueRouter({
  routes
})

export default router
